.preloader {
  width: 100%;
  height: 100%;
  z-index: 2;
  opacity: 1;
  visibility: visible;
  transition: all 1s;
  position: fixed;
  top: 0;
  left: 0;
}

.preloader .loader {
  width: 100px;
  height: 100px;
  perspective: 800px;
  border-radius: 50%;
  position: absolute;
  top: calc(50% - 32px);
  left: calc(50% - 32px);
}

.inner {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  position: absolute;
}

.inner.one {
  border-bottom: 6px solid #ec42f5;
  animation: rotate-one 1s linear infinite;
  top: 0%;
  left: 0%;
}

.inner.two {
  border-right: 6px solid #e6e444;
  animation: rotate-two 1s linear infinite;
  top: 0%;
  right: 0%;
}

.inner.three {
  border-top: 6px solid #42cef5;
  animation: rotate-three 1s linear infinite;
  bottom: 0%;
  right: 0%;
}

@keyframes rotate-one {
  0% {
    transform: rotateX(35deg)rotateY(-45deg)rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg)rotateY(-45deg)rotateZ(360deg);
  }
}

@keyframes rotate-two {
  0% {
    transform: rotateX(60deg)rotateY(10deg)rotateZ(0deg);
  }

  100% {
    transform: rotateX(60deg)rotateY(10deg)rotateZ(360deg);
  }
}

@keyframes rotate-three {
  0% {
    transform: rotateX(35deg)rotateY(55deg)rotateZ(0deg);
  }

  100% {
    transform: rotateX(35deg)rotateY(55deg)rotateZ(360deg);
  }
}

.modal_overlay:before {
  content: "";
  width: 100%;
  height: 100%;
  opacity: .7;
  z-index: 5555;
  background: #000;
  position: fixed;
  top: 0;
  left: 0;
}

.overlay {
  content: "";
  opacity: 0;
  visibility: hidden;
  width: 100%;
  height: 100%;
  z-index: 9999;
  overflow: no-scroll;
  background-color: rgba(0, 0, 0, .8);
  position: fixed;
  top: 0;
  left: 0;
}

